import * as React from "react"
import Layout from "../components/commons/Layout"

const ContactPage = () => {
  return (
    <Layout>
      <section>
        <h1 className="text-3xl font-bold mb-4">
          Contact <span className="text-main">Me</span> ðŸš€
        </h1>
        <div className="mt-8">
          <form className="w-full max-w-md" action="https://formsubmit.co/gianncarlocarrascoperez@gmail.com" method="POST">
            <input type="hidden" name="_subject" value="New email from portfolio" />
            <div className="mb-4">
              <label htmlFor="email" className="block text-gray-400">Email</label>
              <input type="email" id="email" name="email" className="w-full bg-gray-700 text-white border border-gray-600 p-2 rounded focus:outline-none focus:border-blue-500" required />
            </div>
            <div className="mb-4">
              <label htmlFor="message" className="block text-gray-400">Message</label>
              <textarea id="message" name="message" rows="4" className="w-full bg-gray-700 text-white border border-gray-600 p-2 rounded focus:outline-none focus:border-blue-500" required ></textarea>
            </div>
            <div className="text-center">
              <button type="submit" className="bg-emerald-500 text-white py-2 px-4 rounded hover:bg-emerald-600 focus:outline-none focus:ring focus:ring-emerald-300 w-full max-w-md">Send Message</button>
            </div>
          </form>
        </div>
      </section>
    </Layout>
  )
}

export default ContactPage

export const Head = () => <title>Gianncarlo Carrasco | Portfolio</title>
